@media (max-width: 920px) {
    
    .container_visible {
        margin-left: 2.5rem;
        width: 60%;
    
        border: 1px solid #575755;
        border-radius:1rem;
        padding: 1.5rem 2rem 2.5rem 2.5rem;
    
        opacity: 0;
        transform: translateX(20px);
        animation: fadeInRight 5s ease-out forwards;
    }
    .container_visible:hover {
        border: 1px solid #28e98c;;
        opacity: 1;
    }
    
    .header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-top:-2rem;
    }
    .header > h1{
        font-size: 1.4rem;
    }
    .description{
        font-size: .8rem;
        color:#8c8c8c;
    }
    .projects{
        margin-top:1.3rem;
        font-size: .9rem;
        font-family: serif;
    }
    .container_not_visible{
        opacity:0;
    }

    @keyframes fadeInRight {
          from {
            opacity: 0;
            transform: translateX(20px);
          }
          to {
            opacity: 1;
            transform: translateX(0);
          }
        }
    @keyframes scrollText {
        0% {
            transform: translateX(150%);
            opacity:0;
        }
        20% {
            transform: translateX(150%);
            opacity:0;
        }
        30% {
            transform: translateX(150%);
            opacity:0;
        }
        70% {
            transform: translateX(150%);
            opacity:0;
        }
        80% {
            transform: translateX(150%);
            opacity:.2;
        }
        100% {
            transform: translateX(0%);
            opacity:1;
        }
        .complement{
            transform: translateX(0);
            opacity: 1;
        }
    }
    
}
